import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { UsersRoutingModule } from './users-routing.module';
import { UsersComponent } from './users/users.component';
import { UsersPageComponent } from './users/users-page/users-page.component';
import { UserComponent } from './users/user/user.component';
import { SharedModule } from '../../shared/shared.module';
import { UserEditorCardLayoutComponent } from './users/user/user-editor-card-layout/user-editor-card-layout.component';

@NgModule({
  imports: [
    CommonModule,
    UsersRoutingModule,
      SharedModule
  ],
  declarations: [UsersComponent,  UsersPageComponent, UserComponent, UserEditorCardLayoutComponent]
})
export class UsersModule { }



