import {Component, OnInit, Output, EventEmitter} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {UserService} from "../../../../../shared/services/user.service";
import {GeneralService} from "../../../../../shared/services/general.service";
import { roles } from "../../../../../shared/constants";
import { User } from "../../../../../shared/interfaces/user";


@Component({
  selector: 'app-user-editor-card-layout',
  templateUrl: './user-editor-card-layout.component.html',
  styleUrls: ['./user-editor-card-layout.component.scss'],

})
export class UserEditorCardLayoutComponent implements OnInit {
    formGroup: FormGroup;
    processing = false;
    selectingRole = false;
    isFormActive = false;
    roles = roles;
    @Output('userAdded') userAdded: EventEmitter <User> = new EventEmitter();

    constructor(
        private fb: FormBuilder,
        private userService: UserService,
        private generalService: GeneralService
    ) {}

  ngOnInit() {
        this.setupForm();
  }
  setupForm() {
        const controls: any = {};
        controls.email = this.fb.control('', Validators.required);
        controls.password = this.fb.control('', Validators.required);
        controls.role_id = this.fb.control('', Validators.required);
        controls.name = this.fb.control('', Validators.required);
        this.formGroup = this.fb.group(controls);
  }
    getSubmitBtnLabel(){
        if (this.processing) {
            return 'Please wait...';
        }
        return 'Create +';
  }

    addUser() {
        this.processing = true;
        this.userService.addUser(this.formGroup.value.email, this.formGroup.value.password, this.formGroup.value.role_id, this.userService.user.accountId, this.formGroup.value.name).subscribe((response: any ) => {
            if (response.success === true) {
                this.generalService.showNotification('New user is inserted successfully.', 'Okay');
                var user: User = {
                    id: response.userId,
                    name: this.formGroup.value.name,
                    email: this.formGroup.value.email,
                    role: this.generalService.getObjectKeyByValue(this.roles, this.formGroup.value.role_id)

                };
                this.userAdded.emit(user);
                this.reset();

            }
        }, () => {
            this.processing = false;
        }, () => { this.processing = false; });
    }
    reset() {
        this.formGroup.reset();
        this.formGroup.clearValidators();
    }

    onRoleSelectorOpenedChanged(isOpened){
        if(isOpened){
            this.selectingRole = true;
        }else
        {
            this.selectingRole = false;
        }
    }

    showForm(){
        this.isFormActive = true;
    }

    hideForm(){
            setTimeout(() => {
                if(this.selectingRole){
                    return false;
            }else{
                    this.isFormActive = false;
                }

        }, 400);

    }
}



